@import url("https://fonts.googleapis.com/css2?family=Mukta:wght@200;300;400;500;600;700;800&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Mukta", sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
}

.formSection {
  width: 100vw;
  height: 110px;
  background: rgb(2,0,36);
  background: rgb(2,0,36);
  background: linear-gradient(80deg, rgba(2,0,36,1) 0%, rgba(2,0,36,1) 12%, rgba(2,0,36,1) 27%, rgba(38,0,46,1) 39%, rgba(81,0,57,1) 55%, rgba(134,0,71,1) 72%, rgba(177,0,82,1) 86%, rgba(255,0,102,1) 100%);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.formSection .title {
  text-align: center;
}

.formSection input {
  width: 160px;
  height: 25px;
  margin-right: 10px;
  padding-left: 6px;
  outline: none;
  font-size: 16px;
  border: 1px solid rgb(100, 98, 98);
  border-radius: 4px;
}

.formSection button {
  height: 26px;
  width: 50px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
}

.resultImage {
  width: 300px !important;
  cursor: pointer;
  transition: 200ms;
}

.result {
  margin: 10px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(18rem, 1fr));
  grid-auto-rows: 450px;
  grid-auto-flow: dense;
}

.card {
  display: flex;
  align-items: stretch;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: stretch;
  justify-content: flex-start;
}

.card .info {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

.info img {
  margin-right: 10px;
  border-radius: 50%;
  width: 30px;
  height: 30px;
}
.loading {
  text-align: center;
  height: 70vh;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
}
